<template>
    <div class="col-lg-9 col-md-9">
        <ContentHeader :about="product.about" />
        <div class="detail-content">
            <div class="row">
                <div class="col-lg-8 col-md-8" id="_about-product">
                    <div class="_product-image">
                        <carousel v-if="product.image.length" :responsive="{0:{items:1,nav:false,loop:false, autoplay: true}}" class="owl-product">
                            <img v-for="item in product.image" :key="item.index" v-bind:src="api + '/image/product/' + item.name">
                        </carousel>
                        <span class="_product-time">{{ getTime(product.about.date).fromNow() }}</span>
                    </div>
                    <about :product="product.about" />
                    
                </div>
                <div class="col-lg-4 col-md-4">
                    <user :product="product.about" />
                </div>
            </div>
            <div class="row">
                <div class="col-lg-8 col-md-8">
                    <div class="content-body">
                        <div v-html="product.about.content" class="product-content"></div>
                    </div>
                </div>
            </div>
            <div v-if="product.suggest.length" class="row products">
                <div class="hr"></div>
                <div class="col-lg-12">
                    <h3>Tin đăng khác: <span style="color:#FF9800;">{{ product.about.province }} - {{ product.about.category }}</span></h3>
                    <carousel :responsive="{
                        320:{
                            items:2,
                            nav:false,
                            loop:false, 
                            autoplay: true
                        },
                        600:{
                            items:3,
                            nav:false,
                            loop:false, 
                            autoplay: true
                        },
                        1000:{
                            items:4,
                            nav:false,
                            loop:false, 
                            autoplay: true
                        }
                    }" class="product-suggest">
                        <router-link v-for="(item, index) in product.suggest" :key="index" :to="{name:'route.product.index', params:{province: item.url_province, district: item.url_district, category: item.url_category, id: item.id, product: item.url }}"  :title="item.name" class="item">
                            <img  :src="`${api}/image/product/thumbnail/${item.image}`" class="img" :alt="item.name" :title="item.name">
                            <h2 class="title" :title="item.name">{{ item.name }}</h2>
                            <span v-if="item.price!=0" class="price">{{ number_format(item.price,0) }} đ</span>
                            <span v-if="item.price==0" class="price">Thoả thuận</span>
                        </router-link>
                    </carousel>
                </div>
            </div>
        </div>
    </div>            
</template>
<script>
import axios from "axios";
import carousel from 'vue-owl-carousel';
import about from "./about/Index";
import user from "./user/Index";
import moment from "moment";

import ContentHeader from "./ContentHeader";

export default {
    components: {carousel, about, user, ContentHeader},
    data() {
        return {
            api: this.$store.state.domainApi,
            params: this.$route.params,
            product: {
               about: {},
               image: [],
               suggest: []
            }
        }
    },
    methods:{
        number_format( number, decimals, dec_point, thousands_sep ) {
            var n = number, c = isNaN(decimals = Math.abs(decimals)) ? 2 : decimals;
            var d = dec_point == undefined ? "," : dec_point;
            var t = thousands_sep == undefined ? "." : thousands_sep, s = n < 0 ? "-" : "";
            var i = parseInt(n = Math.abs(+n || 0).toFixed(c)) + "", j = (j = i.length) > 3 ? j % 3 : 0;
                                    
            return s + (j ? i.substr(0, j) + t : "") + i.substr(j).replace(/(\d{3})(?=\d)/g, "$1" + t) + (c ? d + Math.abs(n - i).toFixed(c).slice(2) : "");
        },
        getTime(date){
            var time = moment(date);
            return time;
        }
    },
    created(){
        axios.get(this.api + "/public/product/" + this.params.id)
        .then(response=>{
            this.product = response.data;
        });
        moment.locale("vi");
    }
}
</script>